import { Component, OnInit, ViewChild, ElementRef, Renderer2, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class HeaderComponent implements OnInit {

  public static readonly ACTIVE_CLASS = 'active';
  public static readonly SOLICITATIONS = 'solicitations';
  public static readonly TRAVELS = 'travels';
  public static readonly DRIVERS = 'drivers';
  public static readonly VEHICLES = 'vehicles';

  navActive: number;
  activeMenu: string;

  @ViewChild('solicitationsMenu') solicitationsMenu: ElementRef;
  @ViewChild('travelsMenu') travelsMenu: ElementRef;
  @ViewChild('driversMenu') driversMenu: ElementRef;
  @ViewChild('vehiclesMenu') vehiclesMenu: ElementRef;

  private options: { [key: string]: ElementRef; };

  constructor(private router: Router, private renderer: Renderer2) {
    this.activeMenu = '';
    this.options = {};
  }

  ngOnInit() {
    this.inicializeOptions();
    this.menuActive();
  }

  onMenuClick(menuClicked: string): void {
    if (menuClicked && menuClicked !== this.activeMenu && this.options[menuClicked]) {
        if (this.options[this.activeMenu]) {
            this.renderer.removeClass(this.options[this.activeMenu].nativeElement, HeaderComponent.ACTIVE_CLASS);
        }

        this.activeMenu = menuClicked;

        this.renderer.addClass(this.options[this.activeMenu].nativeElement, HeaderComponent.ACTIVE_CLASS);

        this.onMenuOption('/home/' + menuClicked);


    } else if (menuClicked === this.activeMenu) {
      this.onMenuOption('/home/' + menuClicked);
    }
}

inicializeOptions(): void {
    this.options[HeaderComponent.SOLICITATIONS] = this.solicitationsMenu;
    this.options[HeaderComponent.TRAVELS] = this.travelsMenu;
    this.options[HeaderComponent.DRIVERS] = this.driversMenu;
    this.options[HeaderComponent.VEHICLES] = this.vehiclesMenu;
}

onSubmit(navActive: number): void {
    this.navActive = navActive;
}

private menuActive(): void {
    if (this.router.url.includes('/home/solicitations')) {
        this.onMenuClick('solicitations');
    } else if (this.router.url.includes('/app/survey')) {
        this.onMenuClick('surveys');
    } else if (this.router.url.includes('/home/drivers')) {
        this.onMenuClick('drivers');
    } else if (this.router.url.includes('/home/vehicles')) {
        this.onMenuClick('vehicles');
    }
}

  onMenuOption(path) {
    console.log(path);
    this.router.navigate([path]);
  }

}



